@font-face {
    font-family: "Letter Gothic";
    src: url("../../assets/home/font/letter\ gothic\ std.otf") format("woff");
    font-weight: normal;
    font-style: normal;
}

.nav-container {
    position: relative;
    width: 100%;
    height: 6vh;
    display: -ms-flexbox;
    display: flex;
